export * from "./bricks.js";
export default Bricks;

import BricksLsi from './bricks-lsi.js';
Plus4U5.Environment.Lsi = Plus4U5.Environment.Lsi || {};
Plus4U5.Environment.Lsi.Bricks = BricksLsi;

// merge into Plus4U5
